// @flow

import {
  type QueryParams,
  type RecQueryParams
} from '../../types/utils'

export const search = (query: QueryParams, jsonObj: any): Array<any> => {
  // If nothing was requested, return the current object
  if (!query.path) return [jsonObj]
  // Tokenize the search terms
  const keys = query.path.split('.')
  // Remove the root token if exists
  if (keys[0] === '$') keys.shift()
  // Call the recursive search
  return searchRec({ ...query, keys, results: [] }, jsonObj)
}

const searchRec = (query: RecQueryParams, jsonObj: any, parent: any): Array<any> => {
  const { value, results, limit, keys } = query

  // If jsonObj is undefined or we have enough results we can return
  if (
    typeof jsonObj === 'undefined' ||
    (limit && results.length >= limit)) return results

  // If we ran out of keys we check the following possiblities:
  if (!keys.length) {
    // 1. If there is no value we can just return the object we are on
    if (value === undefined) results.push(jsonObj)
    // 2. If the value equals jsonObj, we need to return the parent
    else if (value === jsonObj) results.push(parent)
    // 3. If the jsonObj is an array, we need to decend one last time again
    else if (Array.isArray(jsonObj)) {
      jsonObj.forEach(c => searchRec(query, c, jsonObj))
    }
  } else if (typeof jsonObj === 'object') {
    // Get a mutable version of keys
    let { keys } = query

    // If it's a recursive token, decent into all children
    // And remove the recursive token from the keys array
    if (!keys[0]) {
      keys = keys.slice(1)
      for (const key in jsonObj) {
        searchRec(query, jsonObj[key])
      }
    }

    const childKey = keys[0]
    const childQuery = { ...query, keys: keys.slice(1) }
    const children = childKey === '*' ? jsonObj : [jsonObj[childKey]]
    // if (childKey === '*') {
    for (const key in children) {
      searchRec(childQuery, children[key], jsonObj)
    }
    // } else {
    // Decent into child based on the first key in keys
    // With jsonObj as the parent and shifted keys
    //   searchRec(childQuery, jsonObj[childKey], jsonObj)
    // }
  }

  return results
}
